import React, { useCallback } from 'react';

import Box from '../../components/Box'
import { responsive } from '../../components/ThemeProvider/theme'

const Table = ({ nowData, bg = 'bgGreen' }) => {
  const getStripBg = useCallback((i) => i % 2 ? 'lightGray' : 'white', [])
  return (
    <Box is="table" fontSize="1.143em">
      <Box is="thead">
        <Box is="tr">
          {nowData.th.map((h, i) => (
            <Box
              is="th"
              color={responsive(i ? 'black' : 'white', 'white')}
              bg={responsive(i ? getStripBg(i + 1) : bg, bg)}
              px="0.5em"
              py="1em"
              textAlign={responsive('center', i ? 'right' : 'left')}
              whiteSpace="pre-wrap"
              fontWeight="bold"
              key={i}
            >
              {h}
            </Box>
          ))}
        </Box>
      </Box>
      {nowData.tbody.map((h, i) => (
        <Box is="tbody" key={i}>
          <Box is="tr">
            {h.map((n, k) => (
              <Box
                whiteSpace="pre-wrap"
                is="td"
                px="0.5em"
                py="1em"
                bg={responsive(k ? getStripBg(k + 1) : bg, getStripBg(i))}
                color={responsive(k ? 'black' : 'white', 'black')}
                textAlign={responsive('center', k ? 'right' : 'left')}
                key={k}
              >
                {n}
              </Box>
            ))}
          </Box>
        </Box>
      ))}
    </Box>
  );
};

export default Table;
